<template>
  <AppContainer class="main-page">
    <div class="main-page__header">
      <div class="main-page__header-col" />
      <div class="main-page__header-col main-page__header-title">
        <VTypography font-style="header-2" font-weight="heavy"
          >Форма раскрытия</VTypography
        >
        <VTypography font-style="header-2" font-weight="heavy">{{
          formName
        }}</VTypography>
      </div>
      <div class="main-page__header-col main-page__header-right">
        <AppControl />
      </div>
    </div>

    <LoginForm class="main-page__form" @submit="onSubmit" />

    <div class="main-page__divider" />

    <LogoTextIcon class="main-page__logo" />

    <div class="main-page__loader" v-if="loading">
      <VTypography font-weight="heavy" font-style="header-2">
        ...Загрузка</VTypography
      >
    </div>
  </AppContainer>
</template>

<script>
import LoginForm from "@/components/forms/LoginForm";
import AppControl from "@/components/app/AppControl";
import VTypography from "@/components/ui/Typography";
import AppContainer from "@/components/app/AppContainer";
import LogoTextIcon from "@/components/icons/LogoTextIcon";
import { mapGetters } from "vuex";

export default {
  name: "MainView",
  inheritAttrs: false,
  components: {
    LogoTextIcon,
    AppControl,
    LoginForm,
    VTypography,
    AppContainer,
  },
  methods: {
    onSubmit() {
      this.$router.push(`/item/${this.id}`);
    },
  },
  computed: {
    ...mapGetters("formInfo", { formName: "getName" }),
  },
  data() {
    return {
      loading: false,
      id: this.$route.params.id || "0",
    };
  },
  mounted() {
    if (this.id) {
      this.loading = true;

      this.$store.dispatch("loadForm", this.id).finally(() => {
        this.loading = false;
      });
    }
  },
};
</script>

