<template>
  <div class="base-input" :class="rootClass">
    <textarea
      rows="1"
      ref="input"
      v-bind="$attrs"
      v-if="$attrs.multiline"
      class="base-input__field"
    />
    <input
      ref="input"
      v-bind="$attrs"
      v-if="!$attrs.multiline"
      class="base-input__field"
    />
  </div>
</template>

<script>
import autosize from "@github/textarea-autosize";

export default {
  name: "BaseInput",
  inheritAttrs: false,
  props: {
    rootClass: String,
  },
  data() {
    return {
      autosizeInstance: {},
    };
  },
  beforeUnmount() {
    if (this.$attrs.multiline) {
      this.autosizeInstance.unsubscribe();
    }
  },
  mounted() {
    if (this.$attrs.multiline) {
      this.autosizeInstance = autosize(this.$refs.input);
    }
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.base-input {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.base-input__field {
  padding: 12px 0;
  border: none;
  outline: none;
  resize: none;
  background-color: transparent;
  font-weight: $font-weight-medium;
}
</style>
