import { getFormStatus } from "@/helpers/FormsHelpers";

export const resourcesModule = {
  namespaced: true,
  state: {
    usedExternalResources: false,
    usedInternalResources: false,
    externalResources: [],
    internalResources: {
      russian: "",
      english: "",
    },
  },
  getters: {
    getUsedExternalResources(state) {
      return state.usedExternalResources;
    },
    getUsedInternalResources(state) {
      return state.usedInternalResources;
    },
    getExternalResources(state) {
      return state.externalResources;
    },
    getInternalResources(state) {
      return state.usedExternalResources;
    },
    getFormStatus(state) {
      const hasExternalResources = state.externalResources.length > 0;
      const hasInternalResources =
        !!state.internalResources.russian && !!state.internalResources.english;

      const isFilled =
        (!state.usedExternalResources || hasExternalResources) &&
        (!state.usedInternalResources || hasInternalResources);
      const isNotFilled =
        !state.usedExternalResources &&
        !state.usedInternalResources &&
        !hasInternalResources &&
        !hasExternalResources;

      return getFormStatus({ isFilled, isNotFilled });
    },
  },
  mutations: {
    changeUsedExternalResources(state, value) {
      state.usedExternalResources = value;
    },
    changeUsedInternalResources(state, value) {
      state.usedInternalResources = value;
    },
    addExternalResource(state, resource) {
      const list = state.externalResources.slice();

      list.push(resource);

      state.externalResources = list;
    },
    removeExternalResource(state, index) {
      const list = state.externalResources.slice();

      list.splice(index, 1);

      state.externalResources = list;
    },
    updateExternalResource(state, { index, resource }) {
      const list = state.externalResources.slice();

      list.splice(index, 1, resource);

      state.externalResources = list;
    },
    changeRussianInternalResource(state, value) {
      state.internalResources.russian = value;
    },
    changeEnglishInternalResource(state, value) {
      state.internalResources.english = value;
    },
    setForm(state, data) {
      state.externalResources = data.externalResources;
      state.internalResources = data.internalResources;
      state.usedExternalResources = data.usedExternalResources;
      state.usedInternalResources = data.usedInternalResources;
    },
  },
};
