import { getFormStatus } from "@/helpers/FormsHelpers";

export const disclosureModule = {
  namespaced: true,
  state: {
    hasReleaseBefore: false,
    releaseBeforeDate: undefined,
    releaseBeforeInformation: {
      russian: "",
      english: "",
    },

    hasReleasePlaned: false,
    releasePlanedDate: undefined,
    releasePlanedInformation: {
      russian: "",
      english: "",
    },

    hasReleaseThirdParties: false,
    releaseThirdPartiesDate: undefined,
    releaseThirdPartiesInformation: {
      russian: "",
      english: "",
    },
  },
  getters: {
    getHasReleaseBefore(state) {
      return state.hasReleaseBefore;
    },
    getHasReleasePlaned(state) {
      return state.hasReleasePlaned;
    },
    getHasReleaseThirdParties(state) {
      return state.hasReleaseThirdParties;
    },
    getReleaseBeforeDate(state) {
      return state.releaseBeforeDate;
    },
    getReleasePlanedDate(state) {
      return state.releasePlanedDate;
    },
    getReleaseThirdPartiesDate(state) {
      return state.releaseThirdPartiesDate;
    },
    getReleaseBeforeRussianInformation(state) {
      return state.releaseBeforeInformation.russian;
    },
    getReleasePlanedRussianInformation(state) {
      return state.releasePlanedInformation.russian;
    },
    getReleaseThirdPartiesRussianInformation(state) {
      return state.releaseThirdPartiesInformation.russian;
    },
    getReleaseBeforeEnglishInformation(state) {
      return state.releaseBeforeInformation.english;
    },
    getReleasePlanedEnglishInformation(state) {
      return state.releasePlanedInformation.english;
    },
    getReleaseThirdPartiesEnglishInformation(state) {
      return state.releaseThirdPartiesInformation.english;
    },
    getFormStatus(state) {
      const hasReleaseBeforeDate = !!state.releaseBeforeDate;
      const hasReleasePlanedDate = !!state.releasePlanedDate;
      const hasReleaseThirdPartiesDate = !!state.releaseThirdPartiesDate;

      const hasReleaseBeforeRussianInformation =
        !!state.releaseBeforeInformation.russian;
      const hasReleasePlanedRussianInformation =
        !!state.releasePlanedInformation.russian;
      const hasReleaseThirdPartiesRussianInformation =
        !!state.releaseThirdPartiesInformation.russian;

      const hasReleaseBeforeEnglishInformation =
        !!state.releaseBeforeInformation.english;
      const hasReleasePlanedEnglishInformation =
        !!state.releasePlanedInformation.english;
      const hasReleaseThirdPartiesEnglishInformation =
        !!state.releaseThirdPartiesInformation.english;

      const releaseBeforeFilled =
        hasReleaseBeforeDate &&
        hasReleaseBeforeRussianInformation &&
        hasReleaseBeforeEnglishInformation;

      const releasePlanedFilled =
        hasReleasePlanedDate &&
        hasReleasePlanedRussianInformation &&
        hasReleasePlanedEnglishInformation;

      const releaseThirdPartiesFilled =
        hasReleaseThirdPartiesDate &&
        hasReleaseThirdPartiesRussianInformation &&
        hasReleaseThirdPartiesEnglishInformation;

      const isFilled =
        (!state.hasReleaseBefore || releaseBeforeFilled) &&
        (!state.hasReleasePlaned || releasePlanedFilled) &&
        (!state.hasReleaseThirdParties || releaseThirdPartiesFilled);
      const isNotFilled =
        !state.hasReleaseBefore &&
        !state.hasReleasePlaned &&
        !state.hasReleaseThirdParties &&
        !releaseBeforeFilled &&
        !releaseThirdPartiesFilled;

      return getFormStatus({ isFilled, isNotFilled });
    },
  },
  mutations: {
    changeHasReleaseBefore(state, value) {
      state.hasReleaseBefore = value;
    },
    changeHasReleasePlaned(state, value) {
      state.hasReleasePlaned = value;
    },
    changeHasReleaseThirdParties(state, value) {
      state.hasReleaseThirdParties = value;
    },
    changeReleaseBeforeDate(state, date) {
      state.releaseBeforeDate = date;
    },
    changeReleasePlanedDate(state, date) {
      state.releasePlanedDate = date;
    },
    changeReleaseThirdPartiesDate(state, date) {
      state.releaseThirdPartiesDate = date;
    },
    changeReleaseBeforeRussianInformation(state, value) {
      state.releaseBeforeInformation.russian = value;
    },
    changeReleasePlanedRussianInformation(state, value) {
      state.releasePlanedInformation.russian = value;
    },
    changeReleaseThirdPartiesRussianInformation(state, value) {
      state.releaseThirdPartiesInformation.russian = value;
    },
    changeReleaseBeforeEnglishInformation(state, value) {
      state.releaseBeforeInformation.english = value;
    },
    changeReleasePlanedEnglishInformation(state, value) {
      state.releasePlanedInformation.english = value;
    },
    changeReleaseThirdPartiesEnglishInformation(state, value) {
      state.releaseThirdPartiesInformation.english = value;
    },
    setForm(state, data) {
      state.hasReleaseBefore = data.hasReleaseBefore;
      state.releaseBeforeDate = data.releaseBeforeDate;
      state.releaseBeforeInformation = data.releaseBeforeInformation;

      state.hasReleasePlaned = data.hasReleasePlaned;
      state.releasePlanedDate = data.releasePlanedDate;
      state.releasePlanedInformation = data.releasePlanedInformation;

      state.hasReleaseThirdParties = data.hasReleaseThirdParties;
      state.releaseThirdPartiesDate = data.releaseThirdPartiesDate;
      state.releaseThirdPartiesInformation =
        data.releaseThirdPartiesInformation;
    },
  },
};
