import { createLogger, createStore } from "vuex";

import { sleep } from "@/utils/SystemUtils";
import { HttpClient } from "@/helpers/HttpClient";

import { IS_DEV } from "@/constants/AppConstants";
import { datesModule } from "@/store/modules/datesModule";
import { generalModule } from "@/store/modules/generalModule";
import { authorsModule } from "@/store/modules/authorsModule";
import { formInfoModule } from "@/store/modules/formInfoModule";
import { resourcesModule } from "@/store/modules/resourcesModule";
import { technologyModule } from "@/store/modules/technologyModule";
import { disclosureModule } from "@/store/modules/disclosureModule";
import { applicationModule } from "@/store/modules/applicationModule";
import { organizationsModule } from "@/store/modules/organizationsModule";

const DEVELOPMENT_PLUGINS = IS_DEV ? [createLogger()] : [];

export default createStore({
  state: {},
  getters: {},
  actions: {
    async loadForm(store, id) {
      const httpClient = new HttpClient({});

      const data = await httpClient.get("/mock/items.json");

      // Имитация запроса на сервер
      await sleep();

      const form = data[id];

      if (form) {
        const { forms, ...formInfo } = form;

        console.log(store);

        store.commit("formInfo/setForm", formInfo);
        store.commit("authors/setForm", forms.authors);
        store.commit("general/setForm", forms.general);
        store.commit("resources/setForm", forms.resources);
        store.commit("dates/setForm", forms.dates);
        store.commit("disclosure/setForm", forms.disclosure);
        store.commit("organizations/setForm", forms.organizations);
        store.commit("technology/setForm", forms.technology);
      }
    },
  },
  mutations: {},
  modules: {
    application: applicationModule,

    formInfo: formInfoModule,

    dates: datesModule,
    authors: authorsModule,
    general: generalModule,
    technology: technologyModule,
    disclosure: disclosureModule,
    resources: resourcesModule,
    organizations: organizationsModule,
  },
  plugins: [...DEVELOPMENT_PLUGINS],
});
